@media (max-width: 1330px) {
   

  .blog{
    width: 100%;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: repeat(3, 600px);
    gap: 100px;
    margin-top: 100px;
  }
  .gbl h2, p{
    text-align: left;
    cursor: pointer;
  }
  .gbl h2:hover{
  color: #0ea5e9;
  }
  .gbl p {
    color: var(--seller);
  }
.gbl{
  border: 1px solid var(--box1);
}
.gbl img {
  width: 100%;
}
  .blog-img{
    width: 100%;
    display: flex;
    align-items: center;
    gap: 30px;
  }
  .blog-img p{
    display: flex;
    align-items: center;
    gap: 5px;
    font-weight: 600;
    cursor: pointer;
  }
  .blog-img img{
    width: 25px;
  }
  .gbl h3 {
    text-align: left;
    cursor: pointer;
  }
  .gbl .content{
    padding-left: 20px;

  }
}


@media (max-width: 1110px) {
   

  .blog{
    width: 100%;
