.lines{
  width: 35px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 4px;
  z-index: 100;
}
.line{
  width: 100%;
  height: 3px;
  background-color: #000;
  transition: all .2s linear;
}
#check {
  display: none;
}
#check:checked ~ .extra-navigation{
  top: 170px;
  left: 0;
}
#check:checked +.lines .line:nth-child(1){
  transform: rotate(45deg) translate(5px);
}
#check:checked +.lines .line:nth-child(2){
  transform: rotate(-45deg) translate(4px);
}
#check:checked +.lines .line:nth-child(3){
 display: none;
}
.extra-navigation{
  width: 100%;
