@media (max-width: 1330px) {
    .header-container{
        width: 100%;
        display: flex;
        align-items: center;
        justify-content: space-between;
        
      }
   
      .search{
        display: flex;
        width: 400px;
        height: 40px;
        /* padding: 10px 20px; */
        border:  1px solid #cbd5e1;
        background-color: antiquewhite;
      }
      .search input{
        width: 80%;
        height: 95%;
        outline: none;
        border: none;
        padding-left: 10px;
      }
      .search button{
        width: 20%;
        height: 100%;
        border: none;
        background: #06b6d4;
      }
      
      .icons{
        width: 200px;
        display: flex;
        align-items: center;
        gap: 20px;
      }
      .icons img{
        width: 20px;
      }
      .ico{
        position: relative;
      }
      .tp{
        width: 16px;
        height: 16px;
        font-size: 12px;
        border-radius: 50%;
        text-align: center;
        background-color: var(--headerColor);
        position: absolute;
        top: -22px;
        right: -8px;
      }
      .all{
        width: 200px;
        height: 60px;
        /* padding-top: 20px; */
        margin-top: 10px;
        background-color:#fff ;
        display: flex;
        align-items: center;
        justify-content: space-evenly;
        border-top-left-radius: 10px;
        border-top-right-radius: 10px;
        position: relative;
      }
      .all .imgline{
        width: 25px;
      }
      .all .imgchev{
        width: 14px;
      }
      .all button{
        height: 100%;
        padding: 0 20px;
        font-size: 14px;
        font-weight: 600;
        outline: none;
        border: none;
        background: transparent;
      }
      .all-category{
        position: absolute;
        display: flex;
        flex-direction: column;
        width: 200px;
        top: 60px;
        background-color: #fff;
        z-index: 9;
      }
      .no-category{
        position: absolute;
        display: flex;
        flex-direction: column;
        width: 220px;
        top: -100px;
        display: none;
      }
      
      .cat-item{
        border-bottom: 1px solid #d1d5db;
        width: 100%;
        font-size: 14px;
        height: 40px;
        display: flex;
        align-items: center;
        justify-content: space-between;
      }
      .cat-item:hover {
        background-color: var(--headerColor);
      }
      .nav{
        height: 40px;
        padding: 0 20px;
        display: flex;
        align-items: center;
      }
      .navigation{
        display: flex;
      }
      .h-btn{
        width: 140px;
        height: 40px;
        background-color: var(--seller) ;
        display: flex;align-items: center;
      }
      .h-btn button{
        height: 100%;
        width: 90%;
        background-color: transparent;
        font-size: 14px;
        font-weight: 600;
        color: #fff;
        border: none;
      }
      .grids{
        display: grid;
        gap: 30px;
        text-align: center;
        margin: 20px 70px;
        grid-template-columns: repeat(3, 300px);
        grid-template-rows: repeat(2, 250px);
      }
      .box1{
        grid-column: 1/3;
        grid-row: 1/3;
        background-color: var(--box1);
      }
      .box2{
        grid-row: 1;
        grid-column: 3;
        background: url("../../images/514253-Line-Blue-White.jpg");
      }
      .box3{
        grid-row: 2;
        grid-column: 3;
        display: flex;
        align-items: center;
        justify-content: flex-end;
        background: url("../../images/Jc9DMq.jpg") ;
      }
      .box1{
        display: flex;
        align-items: center;
        justify-content: center;
        gap: 20px;
      }
      .box1 .btn1{
        border-radius: 20px;
        border: none;
        padding: 5px 10px;
        font-size: 12px;
      
      }
       .btn2{
        background-color: transparent;
        border: none;
        border-bottom: 2px solid var(--headerColor);
        padding: 5px 10px;
        font-size: 16px;
      
      }
      .box1 .left{
        display: flex;
        flex-direction: column;
        gap: 20px;
        align-items: flex-start;
        justify-content: baseline;
      }
      .box1 .left span{
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        justify-content: baseline;
      }
      .box1 .left span .s10{
        font-size: 45px;
      }
      
      .box1 .left span .ap {
        font-size: 40px;
      }
      .box1 .left span .sam{
        font-size: 55px;
        font-weight: 700;
      }
      .box2{
        display: flex;
        justify-content: center;
        align-items: center;
        gap: 20px;
      }
      .box2 .rights img{
        width: 120px;
      }
      .lefts{
        width: 50%;
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        gap: 20px;
        padding-left: 10px;
      }
    
       .lefts span {
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        gap: 10px;
      }
      .lefts span div{
        font-size: 20px;
        font-weight: 700;
      }
       .lefts button{
        border-radius: 10px;
        border: none;
        padding: 5px 10px;
        border-bottom: 2px solid var(--headerColor);
      }
      .box3 .rights img {
        width: 60%;
        height: 100%;
        padding-left: 15px;
       /* align-self: baseline; */
      }
      
      
      
      .sec-opt{
        width: 960px;
        height: 100px;
        background-color: #fff;
        display: flex;
        justify-content: space-between;
        margin: 0 auto;
      }
      .bx{
        padding: 0 10px;
        display: flex;
        align-items: center;
        gap: 10px;
        justify-content: space-between;
        font-size: 13px;
      }
      .bx .rt{
        display: flex;
        flex-direction: column;
        align-items: flex-start;
      }
      .bx .fr{
      font-weight: 700;
      }
      .bx .lt img{
       
        width: 30px;
        filter: invert(8%) sepia(100%) saturate(6481%) hue-rotate(246deg) brightness(102%) contrast(143%);
        }
      
        .brend {
          width: 90%;
          margin: 0 auto;
        }
        .brend img{
          width: 30%;
        }
        .flash{
          width: 80%;
          margin: 40px auto;
          height: 400px;
          display: flex;
          justify-content: space-between;
          border:  1px solid red;
        }
        .flash .f-left{
          width: 52%;
        }
        .flash .f-right{
          width: 48%;
          display: flex;
          flex-direction: column;
          gap: 20px;
         padding-left: 10px;
         background-color: beige;
        }
        .flash .f-right p{
          font-size: 22px;
          font-weight: 500;
          text-align: left;
        }
        .flash .f-right span{
          text-align: left;
          font-size: 20px;
          margin-top: 30px;
        }
        .flash .f-right .ph{
          width: 280px;
        }
      
        .store{
          display: flex;
          gap: 15px;
        }
      
      
}

