@media (max-width: 1110px) {
    
  .contacts {
    display: flex;
    justify-content: space-between;
    margin: 100px 0;
  }
  .contacts img{
    width: 30px;
    margin-bottom: 15px;
  }
  .left-c .boxes{
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 20px;
  }
  .left-c {
    display: flex;
    width: 50%;
    height: 100%;
    flex-direction: column;
    gap: 20px;
  }
  .left-c .con{
    text-align: left;
    font-size: 20px;
    font-weight: 600;
  }
  .boxes .bc{
    background-color: #D3EFFF;
  }
  .boxes .bc1{
    background-color: #FFEAE5;
  }
  .boxes .box{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 280px;
    height: 180px;
  }
  .ph{
    font-size: 16px;
    font-weight: 600;
  }

  .adress{
    width: 84%;
    height: 450px;
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    padding-left: 70px;
    background-color: #E7F2EC;
  }
  .adress img{
    position: absolute;
    top: 25px;
    left: 20px;
  }
  .adress .map{
    width: 90%;
    height: 250px;
  }
  .adress h1{
    text-align: left;
  }

  .right-contact {
    width: 44%;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 50px 10px;
    border: 1px solid var(--box1);
    color: var(--seller);
    font-size: 14px;
  }

  .right-contact input{
    width: 300px;
    height: 40px;
    padding-left: 20px;
    font-size: 16px;
    border: 1px solid var(--box1);
    outline: none;
  }
  .right-contact .opt{
    width: 350px;
    display: flex;
    flex-direction: column;
  }
  .right-contact textarea{
    font-size: 16px;
    padding: 10px ;
    border: 1px solid var(--box1);
    outline: none;
  }
  .right-contact button{
    width: 380px;
    height: 50px;
    margin-top: 40px;
    font-size: 16px;
    font-weight: 500;
    background-color: var(--extra4);
    color: var(--white) ;
  }

}


