@media (max-width: 1330px) {
    
  .about{
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  .text-about{
    width: 48%;
    display: flex;
    flex-direction: column;
    justify-content: left;
    align-items:flex-start;
    gap: 30px;
  }
  .text-about ul {
    color: var(--seller);
    text-align: left;
  }
  .text-about span {
    color: var(--seller);
    text-align: left;
  }
  .text-about button {
    padding: 10px 30px;
    background-color: var(--headerColor);
    border: none;
  }
  .ab-img{
    width: 45%;
  }
  .ab-img img{
    width: 100%;
  }
}

@media (max-width: 1110px) {
    
  .about{
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  .text-about{
    width: 45%;
