@media (max-width: 1330px) {
    
  .footers{
    width: 100%;
    border-top: 2px solid #06b6d4;
  }
  .footer{
    margin: 40px 0px;
  }
  .bottom-footer{
    width: 100%;
    display: flex;
    gap: 20px;
    justify-content: space-between;
    border-bottom: 1px solid var(--box1);
  }
  .bottom-footer span{
    color: var(--seller);
  }
  .bottom-footer .span{
    color: var(--seller);
  }
  .ft{
    height: 250px;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    text-align: start;
    gap: 20px;
    width: 250px;
    padding-left: 10px;
  }
  .ft1{
    width: 220px;
  }
.social{
  width: 100%;
  height: 100px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
}


