@media (max-width: 1330px) {
    
  .shopify{
    background-color: #dcfce7;
  }
  .shops{
    width: 100%;
    margin: 40px auto;
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-template-rows: repeat(2, 420px );
    gap: 30px;
  }
  .shopitem{
  position: relative;
  overflow: hidden;
  background-color: #ffffff;
  }
  .shopitem .image{
    width: 70%;
    height: 180px;
    margin: 10px auto;
  }
  .shopitem .text{
    text-align: start;
  }
  .shopitem .price{
    display: flex;
    gap: 20px;
  }
  .shopitem .image img{
    width: 100%;
    height: 100%;
  }
  .content-inf{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    gap: 20px;
    font-weight: 600;
    padding: 25px 15px;
  }
  .content-inf .price .old{
    font-weight: 700;
    font-size: 18px;
  }
  .content-inf .price .new{
    font-weight: 700;
    font-size: 18px;
    color: red;
  }
  .content-inf .star-icon {
    width: 20px;
  }
  .btn-add{
    width: 200px;
    height: 40px;
    /* z-index: 1; */
    background-color: #fbbf24;
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 20px;
    left: 20px;
    position: absolute;
    bottom: -42px;
    transition: all .2s linear;
    cursor: pointer;
  }
  .sale{
    margin-top: 50px;
    width: 100%;
    height: 408px;
  }
  .sale img{
    width: 100%;
  }
  .btn-add div{
    font-weight: 700;
  }
  .shopitem:hover .btn-add{
    bottom: 20px;
    transition: all .2s linear;
  }
  .extra-right {
    width: 50px;
    height: 120px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    position: absolute;
    right: -60px;
    top: 40px;
    transition: all .2s linear;
  }
  .extra-right .img {
    width: 35px;
    height: 35px;
    background-color: rgb(209, 203, 203);
    display: flex;
    align-items: center;
    justify-content: center;
  }
  .extra-right img {
    width: 20px;
    
  }
  .shopitem:hover .extra-right {
    right: 5px;
    transition: all .2s linear;
  }
}


